
import { defineComponent, PropType } from "vue";

export default defineComponent({
  props: {
    onChoseUser: {
      type: Function as PropType<() => void>,
      required: true,
    },
    username: {
      type: String,
      required: true,
    },
    email: {
      type: String,
      required: true,
    },
    isActive: {
      type: Boolean,
      required: false,
    },
    className: {
      type: Array as PropType<string[]>,
      required: false,
    },
  },
  setup() {
    return {};
  },
});
